import Session from './Session'
import Chat from './Chat'

export default {
  components: {
    Session,
    Chat
  },
  computed: {
    sessionId () {
      let sessionId = this.$route.params.sessionId || this.$store.state.currSessionId
      return sessionId
    },
  }
}



